.App {
  text-align: center;

  div.generate-button-div {
    z-index: 10;
    position: absolute;
    display: block;
    right: 3rem;
    width: 11rem;
    height: 3rem;
    top: 1rem;
    background: #565656;
    border-radius: 11px;

    font-style: normal;
    font-weight: 500;
    font-size: 26px;
    color: #ffffff;
    cursor: pointer;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
